import React, { useState } from "react";
import { useDispatch } from "react-redux";
import EditTaskModal from "../EditTaskModal/EditTaskModal";
import Subtask from "../SubTask/SubTask";
import Comment from "../Comments/Comment";
import {
  updateTaskAction,
  updateTaskOrderAction,
  toggleSubtaskCompletedAction,
} from "../../store/taskReducer";
import { Draggable } from "react-beautiful-dnd";
import styles from "./Task.module.scss";

const Task = ({
  task,
  handleFileUpload,
  subtasks,
  addSubTask,
  addComment,
  comments,
  index,
  removeTask,
  taskOrder,
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [isDragging, setIsDragging] = useState(false);
  const dispatch = useDispatch();

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleSaveClick = (editedTask) => {
    dispatch(updateTaskAction(editedTask));
    setIsEditing(false);
  };

  const handleDragStart = () => {
    setIsDragging(true);
  };

  const handleDragEnd = () => {
    setIsDragging(false);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
  };

  const handleDrop = () => {
    if (isDragging) {
      const newTaskOrder = [...taskOrder];
      const currentIndex = newTaskOrder.findIndex((t) => t.id === task.id);
      newTaskOrder.splice(currentIndex, 1);
      newTaskOrder.splice(currentIndex, 0, task);
      dispatch(updateTaskOrderAction(newTaskOrder));
      setIsDragging(false);
    }
  };

  const toggleSubtaskCompleted = (subtaskId) => {
    dispatch(toggleSubtaskCompletedAction(subtaskId));
  };

  const taskStatusOptions = ["Queue", "Development", "Done"];
  return (
    <Draggable draggableId={task.id.toString()} index={index}>
      {(provided) => (
        <li
          className={styles.task}
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}
          onDragStart={handleDragStart}
          onDragEnd={handleDragEnd}
          onDragOver={handleDragOver}
          onDrop={handleDrop}
        >
          {isEditing ? (
            <EditTaskModal
              task={task}
              onSave={handleSaveClick}
              onCancel={() => setIsEditing(false)}
            />
          ) : (
            <div>
              <h3>{task.title}</h3>
              <p>{task.description}</p>
            </div>
          )}

          <p> : {task.id}</p>
          <p> : {task.creationDate}</p>
          <p>  : {task.workTime}</p>
          <p> : {task.endDate}</p>
          <p>: {task.priority}</p>

          <p>
             :{" "}
            <select
              value={task.status}
              onChange={(e) => {
                const newStatus = e.target.value;
                const updatedTask = { ...task, status: newStatus };
                dispatch(updateTaskAction(updatedTask));
              }}
            >
              {taskStatusOptions.map((status) => (
                <option key={status} value={status}>
                  {status}
                </option>
              ))}
            </select>
          </p>
          <p>
             :{" "}
            {task.attachments.map((attachment, index) => (
              <a
                key={index}
                href={attachment}
                target="_blank"
                rel="noopener noreferrer"
              >
                {attachment}
              </a>
            ))}
          </p>
          <input
            type="file"
            multiple
            onChange={(e) => handleFileUpload(e, task.id)}
          />
          <ul>
            {subtasks.map((subtask) => (
              <Subtask
                key={subtask.id}
                subtask={subtask}
                toggleCompleted={toggleSubtaskCompleted}
              />
            ))}
          </ul>
          <div className={styles.task__buttons}>
            <button onClick={() => handleEditClick()}></button>
            <button
              onClick={() =>
                addSubTask(
                  prompt(" "),
                  prompt("  "),
                  task.id
                )
              }
            >
               
            </button>
            <button
              onClick={() => addComment(prompt(" "), task.id)}
            >
               
            </button>
            <ul>
              <h4></h4>
              {comments.map((comment) => (
                <Comment key={comment.id} comment={comment} />
              ))}
              <button onClick={() => removeTask(task.id)}>
                 
              </button>
            </ul>
          </div>
        </li>
      )}
    </Draggable>
  );
};

export default Task;
